.pop-up-container {
  width: 100%;
  height: 100%;
  color: #fff;
  background-color: rgba(17, 17, 17, .592);
  display: none;
  position: fixed;
  top: 0;
}

.pop-up {
  width: 846px;
  height: 598px;
  background-color: #111;
  border-radius: 20px;
  gap: 20px;
  display: flex;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, .42);
}

.pop-up-image {
  width: 50%;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin: 60px 0 60px 60px;
  display: flex;
  overflow: hidden;
  box-shadow: 1px 1px 24px 2px rgba(1, 1, 1, .45);
}

.pop-up-image img {
  width: 100%;
  height: 100%;
}

.pop-up-description {
  width: 50%;
  flex-direction: column;
  justify-content: space-between;
  margin: 60px 60px 60px 0;
  display: flex;
}

.pop-up-description h2 {
  text-align: left;
  font-family: Roboto;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
}

.pop-up-description-data {
  text-align: left;
  color: #b7b7b7;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 500;
  line-height: 18.75px;
}

.pop-up-description-data span {
  color: #fff;
}

.about {
  line-height: 16px;
}

.about-desc {
  color: #f8f8f8;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

.closePopUp {
  width: 24px;
  height: 24px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 40px;
  right: 40px;
}

.buttonAddToMyLibrary {
  width: 140px;
  height: 40px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, 0);
  border-image-source: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-image-slice: 1;
  border-image-width: 1;
  border-radius: 74px;
  margin-bottom: 20px;
  padding: 10px 0;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  transition: background-color .3s;
}

* {
  margin: 0;
  padding: 0;
}

:root {
  --black-color: #000;
  --white-color: #fff;
  --grey-color: #b7b7b7;
  --orange-color: #f87719;
}

